    z-index: 1;
    background-color: transparent;
    width: 340px;
  }
}
.buttons-block__download-button {
  display: inline-block;
  color: #ffffff;
  border-radius: 29px;
  border: 3px solid #ffffff;
  padding: 14px 35px;
